header {
  display: flex;
  justify-content: space-between;
  padding-inline: 20%;
  width: 48%;
  margin: 0 auto;
  padding-top: 2%;
  padding-bottom: 2%;
  height: 20px;
  @media (max-width: 800px) {
    width: 64%;
    padding-inline: 12%;
  }
  @media (max-width: 600px) {
    width: 90%;
    padding-inline: 5%;
  }
  span {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 28px;
    @media (max-width: 1366px) {
      font-size: 18px;
      line-height: 24px;
    }
    @media (max-width: 1024px) {
      font-size: 12px;
      line-height: 20px;
    }
    @media (max-width: 500px) {
      font-size: 11px;
    }
    @media (max-width: 500px) {
      font-size: 10px;
    }
  }
}
.header {
  padding-top: 8%;
  background-size: 100% auto;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  align-items: center;
  @media (max-width: 1024px) {
    background-size: cover;
  }
  a {
    position: relative;
    width: auto;
    top: -4.4vw;

    img {
      width: 14vw;
      @media (max-width: 1024px) {
        min-width: 140px;
      }
      @media (max-width: 800px) {
        min-width: 120px;
      }
    }
  }
  button {
    position: relative;
    top: 14vw;
    background: rgba(7, 22, 31, 0.5);
    border: 2px solid #ffce84;
    border-radius: 12px;
    color: white;
    padding: 1% 4%;
    font-size: 20px;
    cursor: pointer;
    z-index: 9999;
    @media (max-width: 1024px) {
      top: 14em;
      font-size: 14px;
    }
  }
  .shadow {
    position: relative;
    top: 4.4em;
    background: linear-gradient(180deg, rgba(7, 22, 31, 0) 0%, #07161f 74.05%);
    height: 262px;
    width: 100%;
    z-index: 0000;
  }
  z-index: 0000;
}
