    //       margin-left: -6.38%;
    //       margin-right: 7%;
    //     }
    //   }
    // }
  }

  // TEAM
  .team {
    display: flex;
    overflow-x: scroll;
    overflow-y: hidden;
    margin-left: 4%;
    margin-top: -6%;
    @media (max-width: 800px) {
      margin-top: 6%;
    }
    .blockInfo {
      min-width: 25%;
      @media (max-width: 800px) {
        min-width: 30%;
      }
      @media (max-width: 600px) {
        min-width: 40%;
      }
      @media (max-width: 500px) {
        min-width: 45%;
      }
      h1 {
        font-family: "Dela Gothic One", cursive;
        background: -webkit-linear-gradient(
          45deg,
          #ffffff 0%,
          #ffcd95 40%,
          #764204 100%
        );
        background: -moz-linear-gradient(
          45deg,
          #ffffff 0%,
          #ffcd95 40%,
          #764204 100%
        );
        background: linear-gradient(
          45deg,
          #ffffff 0%,
          #ffcd95 40%,
          #764204 100%
        );
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        font-family: Dela Gothic One;
        font-style: normal;
        font-weight: normal;
        font-size: 82px;
        line-height: 110px;
        margin-bottom: 0%;
        @media (max-width: 1200px) {
          font-size: 52px;
          line-height: 52px;
          margin-bottom: 4%;
        }
        @media (max-width: 800px) {
          font-size: 30px;
          line-height: 30px;
        }
      }
      p {
        margin-top: 2%;
        font-family: "Montserrat", sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 28px;
        @media (max-width: 1200px) {
          margin-top: 0%;
          font-size: 14px;
          line-height: 22px;
        }
        @media (max-width: 800px) {
          font-size: 12px;
          line-height: 18px;
        }
      }
    }
    .members {
      position: relative;
      display: flex;
      flex-wrap: wrap;
      margin-top: 9%;
      margin-left: -14%;
      min-width: 150em;
      margin-bottom: -4%;
      @media (max-width: 800px) {
        margin-left: -26%;
        min-width: 135em;
      }
      @media (max-width: 500px) {
        min-width: 115em;
      }
      @media (max-width: 600px) {
        margin-left: -36%;
      }
      @media (max-width: 500px) {
        margin-left: -54%;
      }
      .memberInfo {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-right: 0%;
        margin-left: 0.8%;
        @media (max-width: 800px) {
          // margin-left: -2.8%;
        }
        @media (max-width: 500px) {
          margin-inline: -2%;
        }
        .memberPic {
          width: 90%;
          height: auto;
          @media (max-width: 1200px) {
            width: 70%;
          }
          @media (max-width: 800px) {
            width: 60%;
          }
          @media (max-width: 600px) {
            width: 50%;
          }
        }
        .memberText {
          position: absolute;
          display: flex;
          flex-direction: column;
          width: 6%;
          margin-top: 10%;
          margin-left: 1%;
          @media (max-width: 1200px) {
            margin-top: 8%;
            width: 5%;
          }

          h4 {
            margin: 0;
            font-family: Dela Gothic One;
            font-style: normal;
            font-weight: normal;
            font-size: 19px;
            line-height: 22px;
            @media (max-width: 1200px) {
              font-size: 14px;
              line-height: 18px;
            }
            @media (max-width: 800px) {
              font-size: 12px;
              line-height: 14px;
            }
          }
          span {
            font-family: "Dela Gothic One", cursive;
            font-style: normal;
            font-weight: normal;
            font-size: 13px;
            line-height: 16px;
            letter-spacing: 0.04em;
            text-transform: uppercase;
            color: #c9a061;
            margin: 3% 0;
            @media (max-width: 1200px) {
              font-size: 11px;
              line-height: 13px;
            }
            @media (max-width: 800px) {
              font-size: 9px;
              line-height: 13px;
            }
          }
          img {
            @media (max-width: 800px) {
              width: 20%;
            }
          }
        }
        &:first-child {
          margin-left: 8.4%;
        }
        &:nth-child(n + 7) {
          margin-top: -16%;
          margin-left: 0%;
          margin-right: 0%;
          @media (max-width: 1200px) {
            margin-top: -14%;
          }
          @media (max-width: 800px) {
            // margin-left: -2.8%;
          }
          @media (max-width: 600px) {
            margin-top: -10%;
          }
        }
        &:nth-child(n + 8) {
          margin-left: 2%;
          @media (max-width: 500px) {
            margin-left: -2%;
          }
        }
        &:nth-child(n + 9) {
          margin-left: 4%;
          @media (max-width: 500px) {
            margin-left: 0%;
          }
        }
        &:nth-child(n + 11) {
          margin-left: 3%;
          @media (max-width: 500px) {
            margin-left: -1%;
          }
        }
      }
    }
  }
}
