.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2%;
  border-top: 1px solid #101e27;
  @media (max-width: 800px) {
    padding: 2% 0;
  }
  img {
    @media (max-width: 500px) {
      width: 140%;
    }
  }
  p,
  a {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    color: #c9a061;
    @media (max-width: 600px) {
      font-size: 12px;
    }
    @media (max-width: 400px) {
      font-size: 11px;
    }
  }
}
